import React, { useContext } from "react"
import "./Cart.css"
import Modal from  "../UI/Modal"
import CartContext from "../../store/cart-context"
import CartItem from "./CartItem"

const Cart = (props) => {
    const cartCtx = useContext(CartContext);

    const onAdd = (item) => {
        cartCtx.addItem({...item,amount:1});
    }

    const onRemove = (id) => {
        cartCtx.delItem(id);

    }

    const cartItems = <ul className="cart-items">{cartCtx.items.map((item)=> <CartItem 
        key={item.id} onAdd={onAdd.bind(null,item)} 
        onRemove={onRemove.bind(null,item.id)} name={item.name} 
        price={item.price} amount={item.amount}/>)}</ul>;
    let orderButton = true;
    if(cartCtx.items.length === 0){
        orderButton = false;
    }

    return <Modal hideCart={props.onHideCart}>
        {cartItems}
        <div className="total">
            <span>Total Amount</span>
            <span>{cartCtx.totalAmount.toFixed(2)}</span>
        </div>
        <div className="actions">
            <button onClick={props.onHideCart} className="button--alt">Close</button>
            {orderButton && <button onClick={()=>{props.orderStatus(true)
            props.onHideCart()}}>Order</button>}
        </div>
        </Modal>

}

export default Cart