import React,{useReducer} from "react"
import CartContext from "./cart-context"

const defaultCartState = {
    items:[],
    totalAmount:0
}

const cartReducer = (state,action) =>{
    if(action.type === "ADD"){
        const updatedTotalAmount = state.totalAmount + (action.item.price*action.item.amount)
        const itemIndex = state.items.findIndex((item)=> item.id === action.item.id);
        const getItem = state.items[itemIndex];
        let updatedItems = [...state.items];
        if(getItem){
            const updatedAmount = updatedItems[itemIndex].amount + action.item.amount;
            updatedItems[itemIndex].amount = updatedAmount;     
        }else{
            updatedItems = state.items.concat(action.item);
        }
        
        return {
            items:updatedItems,
            totalAmount:updatedTotalAmount
        }
    }
    if(action.type === "DEL"){
        const indexOfItem = state.items.findIndex((item)=>{
                return item.id === action.id
        })
        const cartItem = state.items[indexOfItem];
        const updatedTotalAmount = state.totalAmount - cartItem.price;
        let updatedItems = [...state.items];
        if(cartItem.amount === 1){
             updatedItems = state.items.filter(item=> item.id !== action.id)
        }else{
            const updatedAmount = cartItem.amount - 1;
            updatedItems[indexOfItem].amount = updatedAmount;
        }

        return {
            items:updatedItems,
            totalAmount:updatedTotalAmount
        }
    }
        return defaultCartState
}
 
const CartProvider = (props) =>{
    const [cartState,dispatchCartAction] = useReducer(cartReducer,defaultCartState);
    const addItemHandler = item => {
        dispatchCartAction({
            type:"ADD",
            item:item
        })

    }

    const delItemHandler = id => {
        dispatchCartAction({
            type:"DEL",
            id:id
        })
    }


    const clearCartHandler = () =>{
        dispatchCartAction({
            type:"CLEAR"
        })
    }

    const cartContext = {
        items: cartState.items,
        totalAmount :cartState.totalAmount,
        addItem: addItemHandler,
        delItem: delItemHandler,
        clearCart:clearCartHandler
    }
    return <CartContext.Provider value={cartContext}>
        {props.children}
    </CartContext.Provider>

}

export default CartProvider