import React,{useRef} from "react"
import Input from "../../UI/Input"
import "./MealItemForm.css"

const MealItemForm = (props)=>{
    const amountRef = useRef();
    
    const addItemToCartHandler=(event)=>{
        event.preventDefault();
        const enteredAmount = amountRef.current.value;
        const enteredAmountNumber = +enteredAmount;
        if(enteredAmount.trim() === "0" || enteredAmountNumber< 1 || enteredAmountNumber>5){
            return
        }
        console.log("Eneterd Amount is"+enteredAmountNumber);
        props.onAddToCart(enteredAmountNumber);
    }

    return <form className="form" onSubmit={addItemToCartHandler}>
     <Input ref={amountRef} label="Amount" input={{
         id:"amount_"+props.id,
         type:"number",
         min:1,
         max:5,
         step:1,
         defaultValue:1
         }}/>
     <button>+ Add</button>
     {/* {!validAmount && <p>Please enter amount between (1-5)</p>} */}
     </form>
}

export default MealItemForm