import React from "react"
import Card from "../UI/Card";
import MealItem from "./MealItem/MealItem";
import "./AvailableMeals.css"


const DUMMY_MEALS = [
    {
      id: 'm1',
      name: 'Paneer Tikka Masala',
      description: 'Cottage Cheese with spicy gravy',
      price: 225,
    },
    {
      id: 'm2',
      name: 'Paneer Butter Masala',
      description: 'Cottage Cheese dipped in gravy with butter on top',
      price: 250,
    },
    {
      id: 'm5',
      name: 'Veg Kadhai',
      description: 'Mixed vegetables with spicy gravy',
      price: 180,
    },
    {
      id: 'm3',
      name: 'Veg Biryani',
      description: 'A Rice bowl with vegetables and whole spices',
      price: 300,
    },
    {
      id: 'm4',
      name: 'Butter Roti',
      description: 'Tandoor Roti with butter on it',
      price: 30,
    },
  ];

const AvailableMeals = () => {
        const mealList = DUMMY_MEALS.map(meal=> <MealItem id={meal.id} name={meal.name} description={meal.description} key={meal.id} price={meal.price}/>)
        return <section className="meals">
            <Card >
            <ul>{mealList}</ul>
            </Card>
            </section>
}

export default AvailableMeals